import { Component, OnInit } from '@angular/core';
import { ContactService } from './contact.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  lat: number = 22.7496;
  lng: number = 75.8955;



  angForm: FormGroup;
  submitted = false;

  constructor( private fb: FormBuilder, private contactService: ContactService) {
   
   }

   ngOnInit() {
    this.angForm = this.fb.group({
      name: ['', Validators.required],
      email: ['', [Validators.required]],
      phone: ['', [Validators.required ]],
      message: ['', [Validators.required, Validators.minLength(100)]]
    });
  }

   // convenience getter for easy access to form fields
   get f() { 
    //console.log(this.angForm.controls);
    
    return this.angForm.controls; 
  }

  sendMail(name, email, phone, message) {
    this.submitted = true;

    if(this.angForm.valid){
      this.contactService.sendEmail(name, email, phone, message).subscribe(success => {
        console.log('Your data is succesfully submitted');
        console.log(success);
       
      
             
      }, error => {
        console.log('Something went wrong');
      });

      alert('SUCCESS!! :-)');
      location.reload();
     
     
    }
 
    else {
      return;
  }

 
  }




}
