import { Component, OnInit } from '@angular/core';
import { PriceService } from './price.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-price',
  templateUrl: './price.component.html',
  styleUrls: ['./price.component.scss']
})
export class PriceComponent implements OnInit {

  priceForm: FormGroup;
  priceForm2: FormGroup;
  priceForm3: FormGroup;
  submitted = false;
  constructor(private fb: FormBuilder, private priceService: PriceService) {
  
   }

  ngOnInit() {
    this.priceForm = this.fb.group({
      


      stanname: ['', Validators.required],
      stanschool: ['', Validators.required],
      stanemail: ['', [Validators.required, Validators.email]],
      stancontact: ['', [Validators.required, Validators.minLength(10)]],
      stanmessage: ['']
      
    });

    this.priceForm2 = this.fb.group({
      


      proname: ['', Validators.required],
      proschool: ['', Validators.required],
      proemail: ['', [Validators.required, Validators.email]],
      procontact: ['', [Validators.required, Validators.minLength(10)]],
      promessage: ['']
      
    });

    this.priceForm3 = this.fb.group({
      


      entername: ['', Validators.required],
      enterschool: ['', Validators.required],
      enteremail: ['', [Validators.required, Validators.email]],
      entercontact: ['', [Validators.required]],
      entermessage: ['']
      
    });

    
  }

     // convenience getter for easy access to form fields
     get f() { 
      //console.log(this.priceForm.controls);
      
      return this.priceForm.controls; 
     
    }

      // convenience getter for easy access to form fields
      get g() { 
        //console.log(this.priceForm.controls);
        
        return this.priceForm2.controls;

      }

        // convenience getter for easy access to form fields
     get i() { 
      //console.log(this.priceForm.controls);
      
      return this.priceForm3.controls; 
    }
  

  sendMail(stanname, stanschool, stanemail, stancontact, stanmessage) {
    
    this.submitted = true;

    if(this.priceForm.valid){
      this.priceService.sendEmail(stanname, stanschool, stanemail, stancontact, stanmessage).subscribe(success => {
        console.log('Your data is succesfully submitted');
        console.log(success);
       
      
             
      }, error => {
        console.log('Something went wrong');
      });
      

      alert('Your data is successfully transmited!! :-)');
      location.reload();
    }
    else {
      return;
  }


 

  }
  sendMail2(proname, proschool, proemail, procontact, promessage) {
    this.submitted = true;

  if(this.priceForm2.valid){
    this.priceService.sendEmail2(proname, proschool, proemail, procontact, promessage).subscribe(success => {
      console.log('Your data is succesfully submitted');
      console.log(success);
     
    
           
    }, error => {
      console.log('Something went wrong');
    });
    

    alert('Your data is succesfully submitted!! :-)');
    location.reload();
  }
  else {
    return;
};

  }

  sendMail3(entername, enterschool, enteremail, entercontact, entermessage) {
    this.submitted = true;
if(this.priceForm3.valid){
  this.priceService.sendEmail3(entername, enterschool, enteremail, entercontact, entermessage).subscribe(success => {
    console.log('Your data is succesfully submitted');
    console.log(success);
   
  
         
  }, error => {
    console.log('Something went wrong');
  });
  

  alert('Your data is succesfully submitted!! :-)');
  location.reload();
}
else {
  return;
}
}



}
