import { Injectable } from '@angular/core';
import { Headers, Http, Response } from '@angular/http';
import { Jsonp } from '@angular/http';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable } from '../../../node_modules/rxjs';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
const apiUrl = 'pricing';

@Injectable({
  providedIn: 'root'
})
export class PriceService {
  sendEmailUrl = '/send_detail';
  sendEmail2Url = '/send_detail2';
  sendEmail3Url = '/send_detail3';
  constructor(private http: Http) { }

  sendEmail(stanname, stanschool, stanemail, stancontact, stanmessage): Observable<any> {
    const uri = `${apiUrl + this.sendEmailUrl}`;
    const obj = {
      stanname: stanname,
      stanschool:stanschool,
      stanemail: stanemail,
      stancontact: stancontact,
      stanmessage: stanmessage
    };
    return this.http.post(uri, obj);
  }

  sendEmail2(proname, proschool, proemail, procontact, promessage): Observable<any> {
    const uri = `${apiUrl + this.sendEmail2Url}`;
    const obj = {
      proname: proname,
      proschool:proschool,
      proemail: proemail,
      procontact: procontact,
      promessage: promessage
    };
    return this.http.post(uri, obj);
  }

  sendEmail3(entername, enterschool, enteremail, entercontact, entermessage): Observable<any> {
    const uri = `${apiUrl + this.sendEmail3Url}`;
    const obj = {
      entername: entername,
      enterschool:enterschool,
      enteremail: enteremail,
      entercontact: entercontact,
      entermessage: entermessage
    };
    return this.http.post(uri, obj);
  }
 
}