import { Component, OnInit } from '@angular/core';
import { PLATFORM_ID, APP_ID, Inject } from '@angular/core';

import { isPlatformBrowser, isPlatformServer } from '@angular/common';

import { DemoService } from './demo.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

//import { DOCUMENT } from '@angular/platform-browser';
//import { LOCAL_STORAGE , WINDOW} from '@ng-toolkit/universal';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

export class AppComponent {
  //title = 'app works!';

  demoForm: FormGroup;
  submitted = false;

  constructor( private fb: FormBuilder, private demoService: DemoService,  @Inject(PLATFORM_ID) private platformId: Object,
    @Inject(APP_ID) private appId: string) {

    this.createDemo();
  }

  createDemo() {
    this.demoForm = this.fb.group({
      
      email1: ['', Validators.required]
      
    });
  }

  get m() { 
    //console.log(this.demoForm.controls);
    
    return this.demoForm.controls; 
  }

  sendMail(email1) {
    this.submitted = true;
    if(this.demoForm.valid){
    this.demoService.sendEmail(email1).subscribe(success => {
      console.log('Demo request sent');
      
    }, error => {
      console.log('Something went wrong');
    });
    alert('Your e-mail has been sent!!!');
   
    
  }

  
  else {
    return;
}

  }

  

  onActivate(event: any) {
    if (isPlatformBrowser(this.platformId)) {
      let scrollToTop = window.setInterval(() => {
        let pos = window.pageYOffset;
        if (pos > 0) {
          window.scrollTo(0, pos - 50); // how far to scroll on each step
        } else {
          window.clearInterval(scrollToTop);
        }
      }, 16);
    }
  }

  eripl_link(): void {
    window.location.href='http://www.ripplesinfomatics.com/';
  }
 
  fb_link() : void {
    window.location.href='https://www.facebook.com/RInfomatics/';
  }
  twitter_link() : void {
    window.location.href='https://twitter.com/RInfomatics/';
  }
  linkedin_link() : void {
    window.location.href='https://www.linkedin.com/company/rinfomatics/';
  }
  insta_link() : void {
    window.location.href='https://www.instagram.com/rinfomatics/';
  }



}

